<!--  -->
<template>
    <div class="tab-control">
    <div v-for="(item,index) in titles"  class="tab-control-item" 
      :class="{active: index == currentIndex}" @click="itemClick(index)"> 
      <span>{{item}}</span>
    </div>
    </div>
</template>

<script>
    export default {
        name:'TabControl',
        props:{
          titles:{
            type:Array,
            default(){
              return[]
            }
          }
        },
        data () {
            return {
              currentIndex:0
       
            };
        },
        components: {},
        computed: {},
        beforeMount() {},
        mounted() {},
        methods: {
          itemClick(index){
            this.currentIndex=index;
            this.$emit('tabClick',index)
          }
        },
        watch: {}

    }

</script>

<style  scoped>
.tab-control{
  display: flex;
  text-align: center;
  font-size: 15px;
   height: 40px;
  line-height: 40px;
  background-color: #fff;
}
.tab-control-item{
  flex:1; 
}
.tab-control-item span{
  padding: 5px
}
.active{
    color: #ff5777;
    
}
.active span{
    border-bottom: 3px solid var(--color-tint);
}


</style>